/* Overall Container for Team */
.team-container {
  padding: 50px 20px;
  text-align: center;
  min-height: 100vh;
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.2);
  height: auto;
  position: relative;
  animation: fadeIn 2s ease-in-out;
}

/* Title for Team Section */
.team-title {
  font-size: 40px;
  color: #2c3e50;
  font-weight: bold;
  text-transform: uppercase;
  margin-bottom: 40px;
  margin-top: 100px;
  position: relative;
  animation: fadeIn 2s ease-in-out;
}

/* Team Members Layout */
.team-members {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 30px;
  height: auto;
  animation: fadeInUp 2s ease-in-out;
}

/* Individual Team Member Styling */
.team-member {
  background: linear-gradient(135deg, #a1c4fd, #c2e9fb);
  border-radius: 10px;
  width: 280px;
  max-width: 90%;
  padding: 25px;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  overflow: hidden;
  position: relative;
  cursor: pointer; /* Add cursor pointer */
}

/* Hover Effect for Team Member */
.team-member:hover {
  transform: translateY(-10px) scale(1.05);
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.2);
}

/* Team Member Image */
.team-member-image {
  width: 120px;
  height: 120px;
  border-radius: 50%;
  object-fit: cover;
  margin-bottom: 20px;
  border: 5px solid #3498db;
  transition: transform 0.3s ease;
}

/* Hover Effect for Image */
.team-member:hover .team-member-image {
  transform: rotate(360deg);
}

/* Team Member Info Section */
.team-member-info {
  padding: 0 10px;
}

/* Name Styling */
.team-member-name {
  font-size: 22px;
  font-weight: 600;
  color: #34495e;
  margin: 10px 0;
  transition: color 0.3s ease;
}

/* Hover Effect for Name */
.team-member:hover .team-member-name {
  color: #3498db;
  text-shadow: 0 0 5px #3498db;
}

/* Position Styling */
.team-member-position {
  font-size: 16px;
  color: #7f8c8d;
  margin-bottom: 15px;
}

/* Description Styling */
.team-member-description {
  font-size: 14px;
  color: #2c3e50;
  line-height: 1.5;
}

/* Fade In Animation for the Team Section */
@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

/* Slide Up Animation for Team Members */
@keyframes fadeInUp {
  0% {
    transform: translateY(50px);
    opacity: 0;
  }
  100% {
    transform: translateY(0);
    opacity: 1;
  }
}

/* Mobile Responsiveness */
@media (max-width: 768px) {
  .team-members {
    flex-direction: row; /* Set to row for items in a line */
    justify-content: space-around; /* Space between items */
    flex-wrap: wrap; /* Allow items to wrap */
  }

  .team-member {
    width: 48%; /* Two items per row */
    margin-bottom: 20px;
    padding: 20px;
  }

  .team-title {
    font-size: 30px;
    margin-bottom: 30px;
  }

  .team-member-name {
    font-size: 20px;
  }

  .team-member-position {
    font-size: 15px;
  }

  .team-member-description {
    font-size: 13px;
  }
}

/* Extra Small Screens (Mobile Phones) */
@media (max-width: 480px) {
  .team-members {
    flex-direction: column; /* Set to row for items in a line */
    justify-content: space-between; /* Space between items */
  }

  .team-member {
    width: auto; /* Three items per row */
    margin-bottom: 20px;
    padding: 15px;
  }

  .team-member-image {
    width: 80px; /* Adjust image size */
    height: 80px;
  }

  .team-title {
    font-size: 25px;
  }

  .team-member-name {
    font-size: 18px;
  }

  .team-member-position {
    font-size: 14px;
  }

  .team-member-description {
    font-size: 12px;
  }
}
